import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.coroutines.*
import MyElement;

data class GameData (
    public val gameHash: String,
    public val imageLinkGetterUrl: String,
    public val adjectiveAnswerHash: String,
    public val nounAnswerHash: String,
    public val adjectiveBank: List<String>,
    public val nounBank: List<String>,
    protected val bankLength: Long
){

     protected constructor(d : dynamic) : this(
         d.gameHash,
         d.imageLinkGetterUrl,
         d.adjectiveAnswerHash,
         d.nounAnswerHash,
         d.adjectiveBank,
         d.nounBank,
         d.bankLength
     )






    companion object {

        suspend public fun FetchNewGame() :GameData{





            val a : GameData =
                window.fetch("https://gameapilocalloyalquote.ffatty.repl.co/game/v1/new")
                    .then { r ->
                        r.json() }
                    .then { j ->
                        GameData(j)
                    }
                    .await<GameData>()


            MyElement.spanWordAdj.hidden = false;
            MyElement.spanWordNoun.hidden = false;

            //console.log(a.gameHash)

            MyElement.imgGameImage.src="images/loading.gif"


            if(a != null){
                println("gamedata gotten")
                return a
            }
            else{
                    println("error fetching gamedata")
                throw Throwable("error fetching gamedata")
            }

        }

        var state :GameState = GameState
    }
}