import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.coroutines.*
import GameState;

open class MyElement(
    public val el :dynamic,
){

    companion object {
        public val imgGameImage:dynamic get() =
           document.getElementById("imgGameImage").asDynamic()

        public val baseDiv:dynamic get() =
            document.getElementById("base").asDynamic()

        public val spanWordAdj:dynamic get() =
            document.getElementById("selectedWordAdj").asDynamic()
        public val spanWordNoun:dynamic get() =
            document.getElementById("selectedWordNoun").asDynamic()

        public val btnSubmit:dynamic get() =
            document.getElementById("btnSubmit").asDynamic()
        public val btnNewGame:dynamic get() =
            document.getElementById("btnNewGame").asDynamic()

        public val ulAdjectiveBank:WordBankUl get() =
            WordBankUl(document.getElementById("WordBankUL_adj").asDynamic())
        public val ulNounBank:WordBankUl get() =
            WordBankUl(document.getElementById("WordBankUL_noun").asDynamic())
    }
}

open class WordBankUl(
    theEl :dynamic,
) : MyElement(theEl) {
    init {
    }

    fun addAllLIs(ws:List<String>) {
        val a = ws.toString().split(",")
        for (i in a)
            addLI(i)
    }
    protected fun addLI(word:String) {
        //val h = this.el.innerHTML.toString();

        //this.el.innerHTML =
        //    h + "<li class=\"wordLI\">${word}</li>"


        var newEl :dynamic? = null
        newEl = el.appendChild(document.createElement("li"))
        newEl.className = "wordLI"
        newEl.innerHTML = word.toString().trim().trim('\n');
        newEl.addEventListener("click")
        {
                //window.alert(word);
                val elparentid = newEl.parentElement.id.toString();
            if(elparentid.contains("adj"))
                    GameState.chosenAdj = word;
            else if(elparentid.contains("noun"))
                    GameState.chosenNoun = word;

            //return nothing
            Unit

        };

    }

    fun clearLIs() {
        this.el.innerHTML = "";
    }


}