import kotlinx.html.div
import kotlinx.html.dom.append
import org.w3c.dom.Node
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.*
import kotlinx.coroutines.*


fun main() {

    var gd :GameData


    window.onload = {




        GlobalScope.launch {

            gd = GameData.FetchNewGame()

            val abank = MyElement.ulAdjectiveBank;
            val nbank = MyElement.ulNounBank;
/*

*/

            //
            //periodically request the endpoint until it is ready:
            var returnedResponse:dynamic? = null;

            do {

                delay(1000)


                    returnedResponse = window.fetch(gd.imageLinkGetterUrl)
                        .then{ r ->
                            r.json()
                        }.await<dynamic>()


                //yah

                MyElement.btnNewGame.hidden = false;

                MyElement.btnNewGame.addEventListener("click") {

                    abank.clearLIs();
                    nbank.clearLIs();
                    abank.addAllLIs(gd.adjectiveBank)
                    nbank.addAllLIs(gd.nounBank)
                    MyElement.imgGameImage.hidden = false;
                    Unit


                }

                //


            }while(returnedResponse == null  ||
                returnedResponse.isReady == false)


            MyElement.btnSubmit.hidden = false;

            //make this a property to do more logic
            MyElement.imgGameImage.src = returnedResponse.imageUrl
            GameState.imageUrl = returnedResponse.imageUrl;






        }





}}




