import MyElement;




object GameState {




        public val GameData: GameData? = null

        private var chosenadj: dynamic? = null
        private var chosennoun: dynamic? = null

    var imageUrl:String? = null;


        var chosenAdj: String?
            get() = chosenadj
            set(v: String?) {
                MyElement.spanWordAdj.innerHTML = v.toString();
                chosenadj = v.toString();
            }

        var chosenNoun: String?
            get() = chosennoun
            set(v: String?) {
                MyElement.spanWordNoun.innerHTML = v.toString();
                chosennoun = v.toString();
            }

        //var hashedChosenAdj;
        //var hashedChosenNoun;


}